.chart-token-selector {
  padding-left: 0.4rem;
  margin-right: 0.5rem;

  .Dropdown-current {
    font-size: 2.1rem;
    font-weight: 700;
    color: white;
  }

  .Dropdown-current-arrow {
    font-size: var(--font-sm);
    color: hsla(0, 0%, 100%, 0.7);
    margin-left: 1rem;
  }

  .Dropdown-options {
    width: 15.8rem;
    top: 4.6rem;
    left: -0.6rem;
  }

  .Dropdown-option {
    font-size: var(--font-sm);
    height: 3.4rem;
    font-weight: 700;
  }
}
